@import "styles/reset";
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300;400;700&display=swap');

body {
  width: 100%;
  height: 100vh;
  font-family: 'Lexend', sans-serif;
  color: #FFFFFF;
  background: url("./assets/images/bg.jpg") no-repeat 0 0 fixed;
}

.box-shadow {
  -webkit-box-shadow: 0px -5px 20px -5px rgba(45, 191, 10, 0.6);
  -moz-box-shadow: 0px -5px 20px -5px rgba(45, 191, 10, 0.6);
  box-shadow: 0px -5px 20px -5px rgba(45, 191, 10, 0.6);
}

.App {
  height: 100%;
  max-width: 1440px;
  margin: 0 auto;

}

